var alert = require('module/popup/alert');

var unsuccessful = function(msg, relativePath) {
    /*alert(msg, {
        ok: function() {
            window.location.href = $_CONFIG.group_domain + relativePath;
        }
    });*/
    //
    alert(msg);
}

var init = function(data) {
    var code;
    var msg = data.message;
    console.log(typeof data.code)
    switch (data.code) {
        case 101:
            code = 'group';
            msg = "";
            break;
        case 102:
            code = 'group';
            msg = "";
            break;
        case 103:
            code = 'group';
            msg = "";
            break;
        case 410:
            code = 'group_dissolve';
            break;
        case 403:
            code = 'group';
            msg = "";
            break;
        case 404:
        case 422:
        case '911918':
            window.location.href = $_CONFIG.i_domain + 'expert/error_' + data.code + '.html';
            return;
        default:
            alert(data.message);
            return;
    }
    var relUrl = 'index/get_error?code=' + code;
    unsuccessful(msg, relUrl);
}
module.exports = {
    init: init
};



