	UE.plugin.register('webapp', function() {
		var me = this;

		function createInsertStr(obj, toEmbed) {
			return !toEmbed ?
				'<img title="' + obj.title + '" width="' + obj.width + '" height="' + obj.height + '"' +
				' src="' + me.options.UEDITOR_HOME_URL + 'themes/default/images/spacer.gif" _logo_url="' + obj.logo + '" style="background:url(' + obj.logo + ') no-repeat center center; border:1px solid gray;" class="edui-faked-webapp" _url="' + obj.url + '" ' +
				(obj.align && !obj.cssfloat ? 'align="' + obj.align + '"' : '') +
				(obj.cssfloat ? 'style="float:' + obj.cssfloat + '"' : '') +
				'/>' :
				'<iframe class="edui-faked-webapp" title="' + obj.title + '" ' +
				(obj.align && !obj.cssfloat ? 'align="' + obj.align + '"' : '') +
				(obj.cssfloat ? 'style="float:' + obj.cssfloat + '"' : '') +
				'width="' + obj.width + '" height="' + obj.height + '"  scrolling="no" frameborder="0" src="' + obj.url + '" logo_url = "' + obj.logo + '"></iframe>'

		}
		return {
			outputRule: function(root) {
				utils.each(root.getNodesByTagName('img'), function(node) {
					var html;
					if (node.getAttr('class') == 'edui-faked-webapp') {
						html = createInsertStr({
							title: node.getAttr('title'),
							'width': node.getAttr('width'),
							'height': node.getAttr('height'),
							'align': node.getAttr('align'),
							'cssfloat': node.getStyle('float'),
							'url': node.getAttr("_url"),
							'logo': node.getAttr('_logo_url')
						}, true);
						var embed = UE.uNode.createElement(html);
						node.parentNode.replaceChild(embed, node);
					}
				})
			},
			inputRule: function(root) {
				utils.each(root.getNodesByTagName('iframe'), function(node) {
					if (node.getAttr('class') == 'edui-faked-webapp') {
						var img = UE.uNode.createElement(createInsertStr({
							title: node.getAttr('title'),
							'width': node.getAttr('width'),
							'height': node.getAttr('height'),
							'align': node.getAttr('align'),
							'cssfloat': node.getStyle('float'),
							'url': node.getAttr("src"),
							'logo': node.getAttr('logo_url')
						}));
						node.parentNode.replaceChild(img, node);
					}
				})

			},
			commands: {
				/**
				 * 
				 * @command webapp
				 * @method execCommand
				 * @remind APPKey
				 * @remind  <a href="http://app.baidu.com/" target="_blank">http://app.baidu.com/</a>
				 * @param { Object } appOptions  key title=> width=>
				 * height=>logo=>logourl=>
				 * @example
				 * ```javascript
				 * //editor
				 * //APP
				 * editor.execCommand( 'webapp' , {
				 *     title: '',
				 *     width: 560,
				 *     height: 465,
				 *     logo: '',
				 *     url: ''
				 * } );
				 * ```
				 */
				'webapp': {
					execCommand: function(cmd, obj) {

						var me = this,
							str = createInsertStr(utils.extend(obj, {
								align: 'none'
							}), false);
						me.execCommand("inserthtml", str);
					},
					queryCommandState: function() {
						var me = this,
							img = me.selection.getRange().getClosedNode(),
							flag = img && (img.className == "edui-faked-webapp");
						return flag ? 1 : 0;
					}
				}
			}
		}
	});

	// plugins/template.js
	///import core
	///import plugins\inserthtml.js
	///import plugins\cleardoc.js
	///commands 
	///commandsName  template
	///commandsTitle  
	///commandsDialog  dialogs\template
	UE.plugins['template'] = function() {
		UE.commands['template'] = {
			execCommand: function(cmd, obj) {
				obj.html && this.execCommand("inserthtml", obj.html);
			}
		};
		this.addListener("click", function(type, evt) {
			var el = evt.target || evt.srcElement,
				range = this.selection.getRange();
			var tnode = domUtils.findParent(el, function(node) {
				if (node.className && domUtils.hasClass(node, "ue_t")) {
					return node;
				}
			}, true);
			tnode && range.selectNode(tnode).shrinkBoundary().select();
		});
		this.addListener("keydown", function(type, evt) {
			var range = this.selection.getRange();
			if (!range.collapsed) {
				if (!evt.ctrlKey && !evt.metaKey && !evt.shiftKey && !evt.altKey) {
					var tnode = domUtils.findParent(range.startContainer, function(node) {
						if (node.className && domUtils.hasClass(node, "ue_t")) {
							return node;
						}
					}, true);
					if (tnode) {
						domUtils.removeClasses(tnode, ["ue_t"]);
					}
				}
			}
		});
	};

