var domUtils = UE.dom.domUtils;

var utils = UE.utils;

var getContent = require('editor/utils/getContent');

var changeTagName = function(editor, tagName, key, value) {
    if (tagName == value) {
        editor.execCommand(key, 'p');
    } else {
        editor.execCommand(key, value);
    }
}

UE.registerUI('h2 h1', function(editor, uiName) {
    //console.log(uiName)
    //command
    /*  editor.registerCommand(uiName, {
         execCommand: function() {
             alert('execCommand:' + uiName)
         }
     }); */
    //button
    var aliasName;
    var args = 0; //
    var key = "";
    var value = "";

    switch (uiName) {
        case 'h1':
            aliasName = "h1";
            args = 1;
            key = "Paragraph";
            value = "h1"
            break;
        case 'h2':
            aliasName = "h2";
            args = 1;
            key = 'Paragraph';
            value = 'h2';
            break;
    }

    var btn = new UE.ui.Button({
        //
        name: aliasName,
        //
        title: aliasName,
        //,icon,icon
        //cssRules: 'background-position: -500px 0;',
        //iconEle: '<em class="insert-imoj iconn-27">' + aliasName + '</em>',
        //
        onclick: function(e) {
            var range = editor.selection.getRange();
            if (getContent(range).indexOf('gmp-ebox') != -1) {
                return false;
            }
            //,
            var startDom = range.startContainer;
            var tag = domUtils.findParentByTagName(startDom, ['h1', 'h2', 'h3', 'p'], true);
            if (!tag) return false;
            var tagName = tag.tagName.toLowerCase();

            switch (tagName) {
                case 'p':
                    editor.execCommand(key, value)
                    break;
                case 'h1':
                    changeTagName(editor, tagName, key, value);
                    break;
                case 'h2':
                    changeTagName(editor, tagName, key, value);
                    break;
                case 'h3':
                    changeTagName(editor, tagName, key, value);
                    break;
                default:
                    break;

            }
            return;

        }
    });
    //
    editor.addListener('selectionchange', function(e) {
        var range = editor.selection.getRange();
        var startDom = range.startContainer;
        var tag = domUtils.findParentByTagName(startDom, ['h1', 'h2', 'p'], true);
        if (!tag) return false;
        var tagName = tag.tagName.toLowerCase();

        if (tagName == uiName) {
            btn.setChecked(1);
        } else {
            btn.setChecked(0);
        }

        /*var state = editor.queryCommandState(uiName);
        console.log(state)
        if (state == -1) {
            btn.setDisabled(true);
            btn.setChecked(false);
        } else {
            btn.setDisabled(false);
            btn.setChecked(state);
        }
        btn.setChecked(1);*/
    });
    //button,button
    return btn;
}, 0);



