var Dialog = require('dialog');



var create = function(options) {
    options = options || {};
    var defaults = {
        title: ' ',
        modal: true,
        fixed: true,
        width: 350,
        content: '<p class="del-pop-p">9</p>',
        className: 'pop-box',
        okValue: '',
        okCls: 'pc-btn pc-btnh35 circle-pop-btn',
        ok: function() {

        },
        onshow: function() {
            $('[i="title"]').hide();
        }
    };
    $.extend(true, defaults, options);
    return Dialog(defaults);
};


module.exports = {
    create: create
};


